import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Subscription } from 'rxjs';
import { BreakpointService } from '../../../utilities/services/breakpoint.service';
import { CardDashboardModel } from './card-dashboard.model';

@Component({
  selector: 'kkl-card-dashboard',
  template: (require('./card-dashboard.component.html').default || require('./card-dashboard.component.html')).length ? (require('./card-dashboard.component.html').default || require('./card-dashboard.component.html')) : '',
  styles: [(require('./card-dashboard.component.scss').default || require('./card-dashboard.component.scss')).length ? (require('./card-dashboard.component.scss').default || require('./card-dashboard.component.scss')) : '']
})
export class CardDashboardComponent implements OnInit {


  @Input() card: CardDashboardModel;
  @Output() onClick = new EventEmitter<CardDashboardModel>();

  private subscription: Subscription;
  constructor(private breakpointService: BreakpointService) {}

  ngOnInit(): void {
    this.subscribeToTablet();
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  public onCardClick() {
    this.onClick.emit(this.card);
  }

  private subscribeToTablet() {
    this.subscription = this.breakpointService
      .isTablet()
      .subscribe((table: boolean) => {
        this.card.size = table ? 6 : 8;
      });
  }
}
