import { Component, Input, OnInit } from '@angular/core';
import { QuestionGroupModel } from '../../components/form/models/question-group.model';
import {
  FormService,
  Question,
} from '../../components/form/services/form.service';

@Component({
  selector: 'app-form-ex',
  template: (require('./form-ex.component.html').default || require('./form-ex.component.html')).length ? (require('./form-ex.component.html').default || require('./form-ex.component.html')) : '',
  styles: [(require('./form-ex.component.scss').default || require('./form-ex.component.scss')).length ? (require('./form-ex.component.scss').default || require('./form-ex.component.scss')) : ''],
})
export class FormExComponent implements OnInit {
  @Input() questions: Question[];

  public group: QuestionGroupModel;

  constructor(private formService: FormService) {}

  ngOnInit(): void {
    this.group = {
      key: 'example',
      label: '',
      questions: this.formService.setQuestionList(this.questions),
      formGroup: this.formService.setFormGroup(this.questions),
    };
  }
}
