import { Component, Input, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import {
  FormService,
  Question,
} from '../../components/form/services/form.service';

@Component({
  selector: 'app-input-ex',
  template: (require('./input-ex.component.html').default || require('./input-ex.component.html')).length ? (require('./input-ex.component.html').default || require('./input-ex.component.html')) : '',
  styles: [(require('./input-ex.component.scss').default || require('./input-ex.component.scss')).length ? (require('./input-ex.component.scss').default || require('./input-ex.component.scss')) : ''],
})
export class InputExComponent implements OnInit {

  @Input() public questions : Question[]
  public question: Question;
  public control: FormControl;

  constructor(private formService: FormService) {}

  ngOnInit(): void {
    const questions = this.formService.setQuestionList(this.questions);
    this.question = questions[0];
    this.control = this.formService.getFieldControl(this.question);
  }
}
